//
// GS1 Toolkit
// --------------------------------------------------

// Variables
// --------------------------------------------------

@import "variables.less";
@import "mixins.less";

// GS1 brand colors

// Legacy color variables @gs1-brand-[color name]
// New color variables @gs1-standard-color-[color name]

// Typography
@gs1-standard-color-blue:                #002c6c;
@gs1-standard-color-dark-gray:           #454545;
@gs1-standard-color-link:                #008dbd;
@gs1-standard-color-light-medium-gray:   #b1b3b3;
@gs1-standard-color-dark-medium-gray:    #888b8d;

// Backgrounds
@gs1-standard-color-white:       #ffffff;
@gs1-standard-color-light-gray:  #f4f4f4;

// Primary Brand Colour Palette
// @gs1-standard-colornavy
@gs1-standard-color-orange:      #f26334;
// @gs1-standard-colordarkgray
// @gs1-standard-colormediumgray

// Secondary Brand Colour Palette & Sector Colour Key
@gs1-standard-color-raspberry:   #f05587; // Retail, Capture
@gs1-standard-color-purple:      #bf83b9; // General Merchandise
@gs1-standard-color-tangerine:   #ff8200; // CPG, Use
@gs1-standard-color-peach:       #fbb034; // Fresh Foods
@gs1-standard-color-sky:         #00b6de; // Healthcare, Identity
@gs1-standard-color-teal:        #22bcb9; // Transport and Logistics
@gs1-standard-color-grass:       #7ac143; // Foodservice, Share
@gs1-standard-color-gold:        #c4b000; // Finance
@gs1-standard-color-mint:        #71b790; // Government
@gs1-standard-color-lime:        #c1d82f; // Agriculture
@gs1-standard-color-lavender:    #af96d4; // Apparel
@gs1-standard-color-terracotta:  #d3875f; // Aerospace & Defence
@gs1-standard-color-olive:       #9dbb68; // Raw Materials/Construction
@gs1-standard-color-mist:        #8db9ca; // Utilities/Fuels
@gs1-standard-color-honey:       #b78b20; // Construction
@gs1-standard-color-forest:      #00b74f; // Recycling
@gs1-standard-color-slate:       #89aadb; // Automotive

// Legacy color names
@gs1-brand-navy:         @gs1-standard-color-blue;
@gs1-brand-darkgray:     @gs1-standard-color-dark-gray;
@gs1-brand-blue:         @gs1-standard-color-link;
@gs1-brand-mediumgray:   @gs1-standard-color-light-medium-gray;
@gs1-brand-white:        @gs1-standard-color-white;
@gs1-brand-lightgray:    @gs1-standard-color-light-gray;
@gs1-brand-orange:       @gs1-standard-color-orange;
@gs1-brand-mediumpink:   @gs1-standard-color-raspberry;
@gs1-brand-mutedpurple:  @gs1-standard-color-purple;
@gs1-brand-brightorange: @gs1-standard-color-tangerine;
@gs1-brand-peach:        @gs1-standard-color-peach;
@gs1-brand-sky:          @gs1-standard-color-sky;
@gs1-brand-teal:         @gs1-standard-color-teal;
@gs1-brand-mediumgreen:  @gs1-standard-color-grass;
@gs1-brand-yellowbrown:  @gs1-standard-color-gold;
@gs1-brand-mint:         @gs1-standard-color-mint;
@gs1-brand-lime:         @gs1-standard-color-lime;
@gs1-brand-lavender:     @gs1-standard-color-lavender;
@gs1-brand-redbrick:     @gs1-standard-color-terracotta;
@gs1-brand-olive:        @gs1-standard-color-olive;
@gs1-brand-bluesteel:    @gs1-standard-color-mist;
@gs1-brand-brown:        @gs1-standard-color-honey;
@gs1-brand-green:        @gs1-standard-color-forest;

// Global text color on `<body>`.
@text-color:            @gs1-standard-color-dark-gray;

// Forms
@form-success:          #5a8f32;
@form-error:            #cc532b;

// Utility
@instruction-bg:     #e5f6fa;

// Font, line-height, and color for body text, headings, and more.
@font-family-sans-serif:  "Gotham SSm A", "Gotham SSm B", Verdana, sans-serif;
@font-family-serif:       Georgia, "Times New Roman", Times, serif;
// Default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
@font-family-monospace:   "Source Code Pro", Menlo, Monaco, Consolas, "Courier New", monospace;
@font-family-base:        @font-family-sans-serif;

@font-size-base:          16px;
@font-size-large:         floor((@font-size-base * 1.4)); // ~22px
@font-size-small:         ceil((@font-size-base * 0.85)); // ~14px

@font-size-h1:            floor((@font-size-base * 3)); // ~48px
@font-size-h2:            floor((@font-size-base * 2.8)); // ~44px
@font-size-h3:            ceil((@font-size-base * 2.5)); // ~40px
@font-size-h4:            ceil((@font-size-base * 1.6)); // ~26px
@font-size-h5:            floor((@font-size-base * 1.4)); // ~22px
@font-size-h6:            ceil((@font-size-base * 1.1)); // ~18px

body { font-family: @font-family-sans-serif; }

// Misc Variables
@gs1-border:            @gs1-standard-color-light-medium-gray;
@modal-side-padding:    75px;
@form-element-height:   34px;
@anchor-offset:         162px;
@list-icon-spacing:     38px;
@section-spacing:       30px;

// Basic styles
// --------------------------------------------------
.toolkit {
  font-size: @font-size-base;
  font-weight: 300;

  figure {
    display: inline-block;
    vertical-align: top;
  }

  figcaption {
    margin-top: 5px;
    text-align: center;
  }

  .thin {
    font-weight: 200 !important;
  }

  //== Paragraph
  p {
    font-size: @font-size-base;
    line-height: 24px;
    color: @gs1-standard-color-dark-gray;
  }

  p.small {
    font-size: @font-size-small;
    line-height: 24px;
  }

  p.large {
    font-size: 18px;
    line-height: 26px;
  }

  strong { 
    font-weight: 500;
  }

  //== Lists

  li {
    line-height: 24px;
  }

  ul.list-left {
    position: relative;
    left: -20px;
  }
  
  ul.downloads {
    padding-left: @list-icon-spacing;

    li {
      line-height: 200%;
      list-style: none;
      position: relative;

      a {
        font-weight: normal;
      }
      
      &:before {
        color: @gs1-standard-color-link;
        content: '\e61d';

        left: (@list-icon-spacing * -1);
        top: 7px;
        position: absolute;
      }
    }
  }

  //== Headings
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: @gs1-standard-color-blue;
    font-weight: 400;
  }

  section {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      &:first-child {
        margin-top: 0;
      }
    }
  }

  h1 {
    font-weight: 400;
    margin: 20px 0 20px;
  }

  h2 {

  }

  h3 {
    margin-top: 45px;
  }

  h4 {
    margin: 35px 0 15px;
  }

  h5 {
    margin-top: 20px;
  }

  .heading-hr {
    border-bottom: 1px solid @gs1-standard-color-light-medium-gray;
    margin-bottom: 20px;
  }

  .subhead {
    margin-top: 0;
  }

  //== Link Anchors
  a {
    color: @gs1-standard-color-link;
    text-decoration: none;
    font-weight: 500;
  }

  a:hover,
  a:focus {
    color: @gs1-standard-color-blue;
  }

  // Homepage
  // --------------------------------------------------
  .gradient {
    margin-top: 0px;
    width: 100%;

    background: #e9f4fb; /* Old browsers */
    background: -moz-linear-gradient(top, #e9f4fb 0%, #ffffff 100%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#e9f4fb), color-stop(100%,#ffffff)); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #e9f4fb 0%,#ffffff 100%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, #e9f4fb 0%,#ffffff 100%); /* Opera 11.10+ */
    background: -ms-linear-gradient(top, #e9f4fb 0%,#ffffff 100%); /* IE10+ */
    background: linear-gradient(to bottom, #e9f4fb 0%,#ffffff 100%); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e9f4fb', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */

    background-repeat: repeat-x;
    background-size: auto 43px;
  }

  .logo img {
    margin-right: 5px;
	height: 43px;
	width: auto;
    @media only screen and (min-width : @screen-sm-min) {
	  height: 61px;
	  margin-right: 30px;
    }
  }
  .logo {
    float: left;
    position: relative;
    @media only screen and (max-width : @screen-md-min) {
      border-bottom: none;
    }
  }

  .logo p,
  .logo h4 {
    display: inline-block;
    @media only screen and (max-width : @screen-md-min) { font-size: 16px; }
  }

  .logo1 {
    position: relative;
    float: left;
    margin-top: 15px;
    p {
      font-size: 16px;
      line-height: 20px;
      position: absolute;
      top: 42px;
      left: 103px;
      width: 300px;
    }
    @media only screen and (min-width : @screen-sm-min) {
      margin-top: 0px;
      padding-bottom: 31px;
      padding-top: 39px;
    }
  }

  .secondary-nav {
    display: none;
    font-size: 12px;

    .form-search-box {
      vertical-align: top;
      font-size: 14px;
    }

    .login-button {
      position: relative;
      vertical-align: top;
    }

    .btn-group {
      margin-left: 30px;
    }

    @media only screen and (min-width : @screen-sm-min) {
      display: block;
      float: right;
      margin-top: 35px;
      text-align: right;

      ul.list-inline {
        margin-bottom: 5px;
        margin-left: 0;

        li {
          padding: 0;

          &.language a {
            color: @gs1-standard-color-dark-gray;
            &:hover {
              color: @gs1-standard-color-blue;
            }
          }

          a {
            border-right: 1px solid @gs1-standard-color-light-medium-gray;
            padding-right: 5px;
          }
        }

        li:last-of-type a {
          border-right: 0;
          padding-right: 0;
        }
      }

      .form-search-box {
        display: inline-block;
      }

      .login-button {
        margin-left: 15px;
        padding-left: 35px;
        padding-right: 15px;

        span[class^=icon] {
          font-size: 18px;
          left: 12px;
          position: absolute;
          top: 8px;
        }
      }
    }

    @media only screen and (min-width : @screen-lg-min) {
      .login-button {
        margin-left: 30px;
        padding-left: 70px;
        padding-right: 42px;

        span[class^=icon] {
          font-size: 18px;
          left: 40px;
          position: absolute;
          top: 8px;
        }
      }
    }
  }

  .page-flag,
  .page-flag-small {
    display: none;
    position: absolute;
    @media only screen and (min-width : @screen-sm-min) {
      background-color: @gs1-standard-color-light-medium-gray;
      display: block;
      top: 0;
      right: 0;
      padding: 30px 25px 15px 25px;
      margin-right: (@grid-gutter-width / 2);
      position: absolute;
    }

    h4 {
      color: #fff;
      margin: 0;
    }
  }

  .page-flag-small {
    font-size: 12px;
    @media only screen and (min-width : @screen-sm-min) {
      padding: 3px 20px;
    }
  }

  .logo1 h5 {
    display: inline;
  }

  .toolbox {
    padding: 55px 0;
  }

  .toolkit-title {
    text-align: center;
  }

  .toolkit-title > h1 {
    margin-top: 0;
    @media only screen and (max-width : @screen-sm-min) { font-size: 30px; }
  }

  .toolkit-buttons a {
    vertical-align: top;
  }

  .toolkit-bottom-container {
    background-color: lighten(@instruction-bg, 3%);
    padding-bottom: 50px;
  }

  .toolkit-bottom-message h1 {
    margin-top: 50px;
    @media only screen and (max-width : @screen-sm-min) { margin-top: 30px; }
  }

  .toolkit-bottom-blocks .toolkit-bottom-code,
  .toolkit-bottom-blocks .toolkit-bottom-styleguide {
    margin-top: 65px;
    float: left;
    background: url('../images/plum.gif') #fff repeat-x;
    box-shadow: 0 -5px 10px #e6e6e6;
  }


  .toolkit-bottom-blocks .toolkit-bottom-code {
    margin-right: 60px;
    @media only screen and (max-width : @screen-sm-min) { margin-right: 0; }
  }

  .toolkit-bottom-blocks .toolkit-bottom-code .circle {
      float: left;
      width: 7px;
      height: 7px;
      border-radius: 4px;
      font-size: 10px;
      line-height: 7px;
      text-align: center;
      background: #fff;
      margin-left: 4px;
      margin-top: 4px;
  }

  .toolkit-bottom-blocks-container {
    float: left;
    margin-right: 130px;
    width: 440px;
    @media only screen and (max-width : @screen-lg-min) { margin-right: 30px; }
    @media only screen and (max-width : @screen-sm-min) {
      margin-right: 0px;
      width: auto;
    }
  }

  .toolkit-bottom-blocks .toolkit-bottom-code,
  .toolkit-bottom-blocks .toolkit-bottom-styleguide {
    width: 440px;
    height: 232px;
    margin-bottom: 38px;
    @media only screen and (max-width : @screen-sm-min) { width: 100%; }
  }

  .toolkit-bottom-blocks .toolkit-bottom-code .toolkit-block1 {
    margin-top: 28px;
    margin-left: 32px;
    @media only screen and (max-width : @screen-sm-min) {
      margin-right: 36px;
      margin-left: 10px;
    }
  }

  .toolkit-bottom-blocks .toolkit-bottom-styleguide .toolkit-block2 {
    margin-top: 42px;
    margin-left: 116px;
  }

  .toolkit-bottom-blocks .toolkit-bottom-styleguide .toolkit-block2 img {
    @media only screen and (max-width : @screen-sm-min) {
      position: relative;
      left: -75px;
    }
  }

  .toolkit-bottom-blocks .toolkit-block-copy {
    height: 485px;
    @media only screen and (max-width : @screen-md-min) { height: auto; }
  }

  .toolkit-bottom-blocks .toolkit-block-copy h4 {
    @media only screen and (max-width : @screen-sm-min) { clear: both; }
  }

  .toolkit-bottom-blocks-container .toolkit-style-guide a { float: left; }

  .toolkit-bottom-blocks-container .toolkit-download {
    @media only screen and (max-width : @screen-sm-min) {
      position: relative;
      top: 40px;
    }
  }

  .toolkit-bottom-blocks-container .toolkit-download,
  .toolkit-bottom-blocks-container .toolkit-style-guide {
    @media only screen and (max-width : @screen-sm-min) { padding-right: 15px; }
    @media only screen and (max-width : @screen-md-min) { margin-top: 20px; }
  }

  // Header
  // --------------------------------------------------

  .header-top {
    background-color: @gs1-standard-color-light-gray;
    border-bottom: 1px solid @gs1-standard-color-light-medium-gray;
    display: none;
    @media only screen and (min-width : @screen-sm-min) {
      display: block;
    }
  }

  .header-bottom {
    background-color: @gs1-standard-color-dark-medium-gray;
    color: @gs1-standard-color-white;
    padding-top: 5px;
    padding-bottom: 5px;
  }

  header {
    box-shadow: 0 -18px 30px 26px rgba(0,0,0, .05);
    left: 0;
    top: 0;
    width: 100%;
    z-index: 10000;

    .container {
      position: relative;
    }

    &.no-gradient {
      box-shadow: none;
    }
  }

  &.header-short {
    .logo1 p {
      top: 38px;
    }
    header:not(.example-header) .logo1 {
      @media only screen and (min-width : @screen-sm-min) {
        padding-bottom: 15px;
        padding-top: 15px;
      }
    }
  }

  // Navigation (container)
  // --------------------------------------------------

  .navigation {
    background-color: #fff;
  }

  // Navbar
  // --------------------------------------------------
  .navbar {
    margin-bottom: 0;
  }

  .navbar li {
    left: 0px;
  }

  .navbar-default {
    background-color: #fff;
    border: 0;

    .navbar-mover li>a {
      color: @gs1-standard-color-blue;
    }
    .navbar-toggle {
      .icon-bar {
        background-color: @gs1-standard-color-orange;
        width: 30px;
        height: 5px;
      }

      &.collapsed .icon-bar {
        background-color: @gs1-standard-color-blue;
      }
    }
  }

  .navbar-toggle {
    border: none;
    &:hover {
      background: none;
    }
  }

  .navbar-default .navbar-nav > li.active {
    @media only screen and (min-width : @screen-md-min) {
      background: url('../images/nav-select.gif') repeat-x;
      background: url(data:image/svg+xml;base64,) repeat-x;
    }
  }

  .navbar-default .navbar-nav > li > a:hover {
    color: @gs1-standard-color-orange;
  }

  .navbar-default .navbar-nav > .active > a,
  .navbar-default .navbar-nav > .active > a:hover,
  .navbar-default .navbar-nav > .active > a:focus {
    color: @gs1-standard-color-orange;
  }

  .navbar-nav {
    > li > a {
      margin-top: 4px;
      margin-bottom: 0px;
      font-weight: 500;
      font-size: 16px;
      @media only screen and (min-width : @screen-md-min) and (max-width : @screen-lg-min) { font-size: 14px; }
      @media only screen and (max-width : @screen-md-min) { margin: 0 !important;  }
    }
  }

  .navbar-default .navbar-nav {
    width: 100%;

    > li {
      margin-right: 20px;
      @media only screen and (min-width : @screen-lg-min) { margin-right: 45px; }
      @media only screen and (min-width : @screen-sm-min) { margin-left: 30px; }
    }
  }

  // Default navbar
  .navbar-default {
//    border-radius: @navbar-border-radius;
    z-index: 3500;

    .navbar-nav > .active > a {
      background: none;
    }
  }

  // Undo rounded corners in static and fixed navbars
  .navbar-static-top,
  .navbar-fixed-top,
  .navbar-fixed-bottom {
    border-radius: 0;
  }

  .navbar-fixed-top {
    // Fixed top nav at extra-small is disabled due to space concerns. This rule will likely go away with the next release of the toolkit with a redesigned small header. See also: .site-content margin changes.
    position: relative;
    @media only screen and (min-width : @screen-sm-min) { position: fixed; }
  }


  // Navigation - Mega Menu
  // --------------------------------------------------

  .yamm {
    &.navbar {
      border: none;
    }

    .navbar-nav {
      margin: 0;

      .section li {
        font-size: 14px;
        margin-left: 0;
        margin-right: 0;

        > a {
          background-color: transparent;
          padding-left: 0;
          padding-right: 0;

          &::focus {
            outline-color: @gs1-standard-color-light-medium-gray;
          }

          &.dropdown-toggle {
            @media only screen and (min-width : @screen-sm-min) {
              padding-right: 8px;
            }
          }
        }
      }

      > li { // top level only
        border-bottom: 1px solid @gs1-standard-color-light-medium-gray;
        margin-left: 0;
        margin-right: 0;
        @media only screen and (min-width : @screen-sm-min) {
          border-bottom: none;
          font-size: 16px;
          margin-right: 40px;
        }
        @media only screen and (min-width : @screen-md-min) {
          margin-right: 45px;
        }

        &:last-of-type {
          border-bottom: none;
          margin-right: 0;
        }

        > a,
        > a:active {
          color: @gs1-standard-color-blue;
          font-size: 18px;
          padding-left: 0;
          padding-right: 0;
          @media only screen and (min-width : @screen-sm-min) {
            padding-bottom: 0;
            font-size: 16px;
          }
        }

        &.dropdown > a {
          padding-right: 8px;
        }
      }

      a {
        &:hover {
          color: @gs1-standard-color-orange;
        }

        &:focus {
          outline-color: @gs1-standard-color-light-medium-gray;
        }

        &.btn-primary:hover {
          color: #fff;
        }
      }

      .active .section li {
        > a,
        > a:hover,
        > a:active {
          background-color: transparent;
        }
      }

      .secondary-nav {
        display: block;

        @media only screen and (min-width : @screen-sm-min) {
          display: none;
        }
      }

      .nav-divider {
        background-color: transparent;
        border-top: 1px solid @gs1-standard-color-light-medium-gray;
        height: auto;
        margin: 0;
        overflow: auto;
      }
    }

    p {
      font-size: 14px;
      line-height: 20px;
      @media only screen and (min-width : @screen-md-min) {
        font-size: 16px;
        line-height: 24px;
      }
    }

    .navbar-header {
      background-color: @gs1-standard-color-light-gray;
      border-bottom: 1px solid @gs1-standard-color-light-medium-gray;
      margin-left: (-@grid-gutter-width / 2);
      margin-right: (-@grid-gutter-width / 2);

      @media only screen and (min-width : @screen-sm-min) {
        display: none;
      }

      .logo1 {
        margin-top: 12px;
        margin-left: (@grid-gutter-width/2);
      }

      .secondary-nav {
        display: inline-block;
        float: right;
        margin-top: 28px;

        .search {
          color: @gs1-standard-color-orange;
          padding: 0px 10px;
          span {
            font-size: 24px;
          }
        }

        .login-button {
          padding: 3px 5px;

          [class^='icon'] {
            font-size: 12px;
          }
        }
      }
    }

    .navbar-collapse {
      border-top: 0;
      margin: 7px 0;
      padding-left: 0;
      padding-right: 0;
      @media only screen and (min-width : @screen-sm-min) {
        margin: 0;
      }
    }

    &.navbar-default {
      z-index: 3000;
    }

    &.navbar-default .navbar-nav {
      padding-bottom: 15px;
      overflow-x: hidden;

      li.open {
        > a,
        > a:hover {
          background-color: transparent;
        }
      }
    }

    .navbar-toggle,
    .navbar-toggle:focus {
      background-color: transparent;
      border: 0;
      margin-bottom: 20px;
      margin-right: 5px;
      margin-top: 20px;
    }

    .logo {
      background-color: @gs1-standard-color-light-gray;
      height: 140px;
      padding-top: 35px;
    }

    .dropdown-toggle,
    .dropdown-toggle:hover {
      background-color: transparent;
      border: none;
      height: auto;
    }

