  // Color styles
  // --------------------------------------------------

  .color-overrides(@colorName, @color, @darken: 0%, @lighten: 0%) {
    .bg-@{colorName} { background-color: @color !important; }
    .color-@{colorName} { color: @color !important; }
    .color-@{colorName}-hover {
      &:hover,
      &:active,
      &:focus {
        color: @color !important;
      }
    }
    .top-border-@{colorName} { border-top-color: @color !important; }

    .bg-@{colorName}-active, .btn-primary.bg-@{colorName}:hover { background-color: darken(@color, @darken) !important; }
    .bg-@{colorName}-disabled, .bg-@{colorName}-disabled:hover { background-color: lighten(@color, @lighten) !important; }
  }

