  // Jumbotron
  // --------------------------------------------------
  .jumbotron { padding: 0; }

  .jumbotron .container,
  .jumbotron img {
    width: 100%;
    padding: 0;
  }

  .jumbotron-spacing {
    margin: 40px 0;
    padding: 40px 0;
  }

  .jumbotron-background {
    background-position: 50% 50%;
    background-size: cover;
  }

  // Footer
  // --------------------------------------------------

  footer {
    margin: 20px 0;

    h4 {
      margin-top: 0;
    }

    p {
      display: block;
      margin: 0;
      font-size: 12px;
    }

    a {
      font-weight: 300;
    }
    
    ul {
      list-style-type: none;
      margin-bottom: 20px;
      padding: 0;

      li {
        line-height: 1em;
        margin-bottom: 7px;
        & > a {
          font-size: 0.875em;
        }
      }
    }

    .icon { font-size: 40px; }

    .footer-links {
      font-size: 0.875em;
      margin-top: 20px;
      margin-bottom: 20px;

      hr {
        margin-bottom: 15px;
        margin-top: 0;
      }

      .link-pipe {
        color: @gs1-standard-color-light-medium-gray;
        margin: 0 15px;
      }
    }

    .social {
      a {
        color: @gs1-standard-color-blue;
        &:hover {
          color: @gs1-standard-color-link;
        }
      }
    }
  }


  // Back to Top
  // --------------------------------------------------

  .navbar-fixed-top + .content-container {
    margin-top: 70px;
  }
  .content-container {
    margin: 0 130px;
  }

  #top-link-block {
    z-index: 5000;
    .affix-top {
      position: absolute; /* allows it to "slide" up into view */
      bottom: -82px; /* negative of the offset - height of link element */
      left: 10px; /* padding from the left side of the window */
    }
    .affix {
      position: fixed; /* keeps it on the bottom once in view */
      bottom: 18px; /* height of link element */
      left: 10px; /* padding from the left side of the window */
    }
  }


  // Utilities
  // --------------------------------------------------
  .vertical-center-outer {
    display: table;
    overflow: hidden;
  }

  .vertical-center-inner {
    display: table-cell;
    vertical-align: middle;
  }


