    .logo-image {
      overflow: hidden;
      margin-top: 10px;
      margin-bottom: 30px;
    }

    .logo-circle {
      width: 150px;
      height: 150px;
      border-radius: 150px;
      font-size: 16px;
      text-align: center;
      color: @gs1-standard-color-white;
      display: inline-block;
      vertical-align: top;
      float: left;
      position: relative;
      margin: 10px 10px 10px 0;

      .color-description {
        position: absolute;
        top: 50%;
        left: 50%;
