        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        width: 100%;
      }

      .title {
        color: @gs1-standard-color-dark-gray;
        font-size: .7em;
        left: 0;
        line-height: 1.3em;
        position: absolute;
        top: 160px;
        width: 150px;
      }

      &.bg-white {
        border: 1px solid @gs1-standard-color-dark-gray;
        color: @gs1-standard-color-dark-gray;
      }

      &.bg-lightgray {
        color: @gs1-standard-color-dark-gray;
      }
    }
    .captions .logo-circle {
      margin-bottom: 60px;
    }

    header.example-header {
      box-shadow: none;
      position: static;
    }

    .yamm .btn-download {
      margin-top: 20px;
      @media only screen and (min-width : @screen-sm-min) and (max-width : @screen-md-min) {
        padding-right: 16px;
        .icon-download {
          display: none;
        }
      }
    }

    .screenshot {
      border: 1px solid @gs1-standard-color-light-medium-gray;
      margin-bottom: 40px;
    }

    .actions {
      @media only screen and (min-width : @screen-sm-min) {
        float: left;
      }
    }

    .setting {
      float: left;
      margin-top: 10px;
      @media only screen and (min-width : @screen-sm-min) {
        float: right;
        margin-top: 0;
      }
      label {
        font-size: 12px;
        font-weight: normal;
      }
    }

    figure {
      margin-top: 20px;
    }

    img.preview {
      border: 1px solid @gs1-standard-color-light-medium-gray;
    }
    
    .bg-light-gray img.preview {
      border-color: @gs1-standard-color-dark-medium-gray;
    }

    .example-button-pointer {
      display: block;
      position: relative;
      top: -15px;
      left: 20px;
    }
  }

  &.documentation {

    .logo2 {
      @media only screen and (min-width : @screen-md-min) {
        background-color: @gs1-standard-color-link;
      }
    }

    table.status {
      width: 100%;
    }
    table.status > tbody > tr:first-child > td {
      border-top: none;
    }
    table.status tr {
      td {
        text-align: right;
        &:first-child {
          text-align: left;
        }
      }
    }

    .section-spacing:last-of-type {
      border-bottom: none;
    }

	.video {
	  margin: 70px auto;
	  width: 780px;
	  p {
	    margin-top: 35px;
	  }
	}
  }

  &.brand-home {
    .intro {
      &.site-content {
        padding-top: 0;
      }
      .container {
        background: url(../images/world-background.png) no-repeat 10% 14%;
        height: 200px;
      }
      h1 {
        font-size: 3.4em;
        font-weight: 200;
      }
      p {
        font-size: 1.5em;
        line-height: 120%;
        letter-spacing: 1px;
        margin-bottom: 25px;
        @media only screen and (min-width : @screen-sm-min) {
          font-size: 3em;
        }
      }
    }

    .section-spacing {
      border: none;
    }
  }
