// Grid utilities

// Outside padding-less layouts
// --------------------------------------------------
.no-outside-padding-columns() {
  .col-left(@index) when (@index = 1) { // initial
    @item-left: ~".flush-outside > .col-xs-@{index}:first-child, .flush-outside > .col-sm-@{index}:first-child, .flush-outside > .col-md-@{index}:first-child, .flush-outside > .col-lg-@{index}:first-child";
    .col-left((@index + 1), @item-left);
  }
  .col-left(@index, @list-left) when (@index =< @grid-columns) { // general; "=<" isn't a typo
    @item-left: ~".flush-outside > .col-xs-@{index}:first-child, .flush-outside > .col-sm-@{index}:first-child, .flush-outside > .col-md-@{index}:first-child, .flush-outside > .col-lg-@{index}:first-child";
    .col-left((@index + 1), ~"@{list-left}, @{item-left}");
  }
  .col-left(@index, @list-left) when (@index > @grid-columns) { // terminal
    @{list-left} {
      padding-left: 0;
    }
  }

  .col-right(@index) when (@index = 1) { // initial
    @item-right: ~".flush-outside > .col-xs-@{index}:last-child, .flush-outside > .col-sm-@{index}:last-child, .flush-outside > .col-md-@{index}:last-child, .flush-outside > .col-lg-@{index}:last-child";
    .col-right((@index + 1), @item-right);
  }
  .col-right(@index, @list-right) when (@index =< @grid-columns) { // general; "=<" isn't a typo
    @item-right: ~".flush-outside > .col-xs-@{index}:last-child, .flush-outside > .col-sm-@{index}:last-child, .flush-outside > .col-md-@{index}:last-child, .flush-outside > .col-lg-@{index}:last-child";
    .col-right((@index + 1), ~"@{list-right}, @{item-right}");
  }
  .col-right(@index, @list-right) when (@index > @grid-columns) { // terminal
    @{list-right} {
      padding-right: 0;
    }
  }

  .col-left(1); // kickstart left
  .col-right(1); // kickstart right
}
.no-outside-padding-columns();